/* Container */
.search__container {
  position: relative;
  width: 100%;
  max-width: 500px;
  /* limit width on large screens */
  margin: 0 auto;
  display: flex;
}

/* Input */
.search__input {
  width: 100%;
  padding: 12px 50px 12px 16px;
  /* leave space for button */
  border: 1px solid #ddd;
  border-radius: 30px;
  font-size: 16px;
  outline: none;
  transition: all 0.3s ease;
}

.search__input:focus {
  border-color: #007bff;
  box-shadow: 0 0 6px rgba(0, 123, 255, 0.3);
}

/* Button */
.search__btn {
  position: absolute;
  right: 8px;
  top: 50%;
  transform: translateY(-50%);
  background: white;
  color: black;
  border: none;
  padding: 8px 14px;
  border-radius: 50%;
  cursor: pointer;
  font-size: 16px;
  line-height: 1;
  transition: background 0.3s ease;
}

/* ðŸ”Ž Responsive tweaks */
@media (max-width: 768px) {
  .search__input {
    font-size: 14px;
    padding: 10px 45px 10px 14px;
  }

  .search__btn {
    padding: 6px 12px;
    font-size: 14px;
  }
}

@media (max-width: 480px) {
  .search__container {
    max-width: 100%;
    /* take full width on small screens */
  }
}
.static-hero-s3 {
  margin-top: 0px !important;
}
